import {
	APPROVER_ID,
	GUEST_TYPES,
	REQUESTED_STATUS_TYPES,
} from 'src/static/mobile-key-status';
import createToken, { getToken } from 'src/services/create-token';
import sendEmail from 'src/services/send-email';
import { formatddMMyyyy24Hours } from './date';
import updateComments from 'src/services/update-comments';

export const getPrimaryEmailId = (rows) => {
	let primaryEmailId = '';
	const row = rows.find((data) => data.guestType === GUEST_TYPES.primary);
	if (row && row.emailId) {
		primaryEmailId = row.emailId;
	}
	return primaryEmailId;
};

export const getRequestBody = (row) => {
	let requestBody = {
		"firstName": "",
		"lastName": "",
		"bookingId": "",
		"emailId": "",
		"hotelCode": "",
		"hotelName": "",
		"endDate": "",
		"beginDate": "",
		"cardNumber": "",
		"langId": "",
		"deviceId": "1",
		"roomNumber": "",
		"primaryMemberId": ''
	};
	if (row.requestBody) {
		requestBody = JSON.parse(row.requestBody);
		delete requestBody.guestEmailId;
		delete requestBody.emailId
	}
	return requestBody;
};

export const prepareBody = ({ row, status, id, rows, bookingId }) => {
	let body = {}
	const requestBody = getRequestBody(row)
	if (status === REQUESTED_STATUS_TYPES.approved) {

		const primaryEmailId = getPrimaryEmailId(rows);
		body = {
			...requestBody,
			primaryEmailId,
			secondaryEmailId: id,
			approvalStatus: status,
			approvalTime: formatddMMyyyy24Hours(new Date()),
			approverId: APPROVER_ID,
		}
	} else {
		body = {
			approvalStatus: status,
			secondaryEmailId: id,
			approvalTime: formatddMMyyyy24Hours(new Date()),
			approverId: APPROVER_ID,
			bookingId: bookingId || '',
		}
	}
	return body
}


export const updateStatusForUser = async ({ requestBody }) => {
	try {
		const token = await getToken();
		await sendEmail({ token, requestBody })
	} catch (err) {
		throw new Error(err)
	}
}


export const updateCommentsForUser = async ({ requestBody }) => {
	try {
		const token = await getToken();
		await updateComments({ token, requestBody })
	} catch (err) {
		throw new Error(err.message)
	}
}
